import React from "react";

import { Title } from "bloomer";
import { Subtitle } from "bloomer/lib/elements/Subtitle";

function ResponsiveTemplate(props) {
  function printDateTime(eventCard) {
    let dateTime = eventCard.date ? eventCard.date : "yyyy-mm-dd";
    dateTime += ", ";
    dateTime +=
      eventCard.startTime !== "" ? convert24to12(eventCard.startTime) : "start";
    dateTime += " - ";
    dateTime +=
      eventCard.endTime !== "" ? convert24to12(eventCard.endTime) : "end";

    return dateTime;
  }

  function convert24to12(time) {
    // Check correct time format and split into components
    time = time
      .toString()
      .match(/^([01]\d|2[0-3])(:)([0-5]\d)(:[0-5]\d)?$/) || [time];

    if (time.length > 1) {
      // If time format correct
      time = time.slice(1); // Remove full string match value
      time[5] = +time[0] < 12 ? "AM" : "PM"; // Set AM/PM
      time[0] = +time[0] % 12 || 12; // Adjust hours
    }
    return time.join(""); // return adjusted time or original string
  }

  return (
    <div
      style={{ maxWidth: "600px", margin: "0 auto" }}
      className="email-container"
    >
      <table
        align="center"
        role="presentation"
        cellSpacing="0"
        cellPadding="0"
        border="0"
        width="100%"
        style={{ margin: "auto" }}
      >
        <tbody>
          <tr>
            <td valign="top" className="bg_white">
              <table
                role="presentation"
                border="0"
                cellPadding="0"
                cellSpacing="0"
                width="100%"
              >
                <tbody>
                  <tr>
                    <td>
                      <div
                        style={{ padding: "5px", backgroundColor: "#d22820" }}
                      >
                        <img
                          src=" https://marxist.ca/img/flag-inverse-en-logo.svg"
                          alt="Fightback"
                          style={{
                            width: "100%",
                            height: "auto",
                            margin: "auto",
                            display: "block"
                          }}
                        />
                      </div>
                    </td>
                  </tr>
                </tbody>
              </table>
            </td>
          </tr>
          {props.thisWeek ? (
            <tr>
              <td>
                <div className="text" style={{ textAlign: "center" }}>
                  <Title isSize={1}>{props.emailTitle}</Title>
                </div>
              </td>
            </tr>
          ) : (
            <tr />
          )}
          {props.thisWeek ? (
            <tr>
              <td>
                <div className="text" style={{ textAlign: "center" }}>
                  <Subtitle isSize={3}>{props.emailSubtitle}</Subtitle>
                </div>
              </td>
            </tr>
          ) : (
            <tr>
              <td>
                <br />
              </td>
            </tr>
          )}

          {props.eventCards.map((eventCard, index) => (
            <tr key={index}>
              <td>
                <img
                  src={
                    eventCard.imageURL
                      ? eventCard.imageURL
                      : "https://via.placeholder.com/480x240"
                  }
                  alt={eventCard.title}
                  style={{
                    width: "100%",
                    maxWidth: "600px",
                    height: "auto",
                    margin: "auto",
                    display: "block"
                  }}
                />
                <div>
                  <h3 style={{ textAlign: "center" }}>
                    {eventCard.facebookURL ? (
                      <a href={eventCard.facebookURL}>
                        {eventCard.title ? eventCard.title : "Title"}
                      </a>
                    ) : (
                      <div>{eventCard.title ? eventCard.title : "Title"}</div>
                    )}
                  </h3>
                </div>
                <div>
                  <p>
                    <b>WHEN:</b>&nbsp;{printDateTime(eventCard)}
                  </p>
                  <p>
                    <b>WHERE:</b>&nbsp;
                    {eventCard.location ? eventCard.location : "TBD"}
                  </p>
                  <p>{eventCard.description ? eventCard.description : ""}</p>
                </div>
              </td>
            </tr>
          ))}
        </tbody>
      </table>
      <br />

      <div
        style={{
          height: "70px",
          backgroundColor: "#d22820",
          padding: "20px",
          color: "white"
        }}
      >
        <table
          role="presentation"
          border="0"
          cellPadding="0"
          cellSpacing="0"
          width="100%"
          height="100%"
        >
          <tbody>
            <tr>
              <td style={{ verticalAlign: "middle" }}>
                <a
                  href="https://www.marxist.ca"
                  style={{ color: "black", textAlign: "center" }}
                >
                  www.marxist.ca
                </a>
              </td>
              <td style={{ verticalAlign: "middle" }} align="right" width="15%">
                <a href="https://www.instagram.com/socialistfightback/">
                  <img
                    src="https://i.imgur.com/sIoeTpM.png"
                    alt="Instagram"
                    width="38"
                    height="38"
                    style={{ display: "block" }}
                    border="0"
                  />
                </a>
              </td>
              <td style={{ verticalAlign: "middle" }} align="right" width="15%">
                <a href="https://www.facebook.com/CanadaMarxists/">
                  <img
                    src="https://i.imgur.com/H5Svz3B.png"
                    alt="Facebook"
                    width="38"
                    height="38"
                    style={{ display: "block" }}
                    border="0"
                  />
                </a>
              </td>
              <td style={{ verticalAlign: "middle" }} align="right" width="15%">
                <a href="https://twitter.com/CanadaMarxists">
                  <img
                    src="https://i.imgur.com/4h4WEng.png"
                    alt="Twitter"
                    width="38"
                    height="38"
                    style={{ display: "block" }}
                    border="0"
                  />
                </a>
              </td>
            </tr>
          </tbody>
        </table>
      </div>
    </div>
  );
}

export default ResponsiveTemplate;
