import React from "react";

import {
  Box,
  Title,
  Delete,
  Label,
  Field,
  Control,
  Input,
  TextArea
} from "bloomer";

function EventCard(props) {
  return (
    <Box className="EventCard">
      <header>
        <Title isSize={4}>{props.title ? props.title : "Title"}</Title>
        <Delete onClick={() => props.deleteMe(props.index)} isSize="large" />
      </header>

      <Field>
        <Label>Title</Label>
        <Control>
          <Input
            type="text"
            value={props.title}
            onChange={e =>
              props.updateValue(props.index, "title", e.target.value)
            }
          />
        </Control>
      </Field>
      <Field>
        <Label>Date</Label>
        <Control>
          <Input
            type="date"
            value={props.date.valueAsDate}
            onChange={e =>
              props.updateValue(props.index, "date", e.target.value)
            }
          />
        </Control>
      </Field>
      <Field>
        <Label>Start Time</Label>
        <Control>
          <Input
            type="time"
            title="Type a to selct AM or p to select PM"
            value={props.startTime}
            onChange={e =>
              props.updateValue(props.index, "startTime", e.target.value)
            }
          />
        </Control>
      </Field>
      <Field>
        <Label>End Time</Label>
        <Control>
          <Input
            type="time"
            title="Type a to selct AM or p to select PM"
            value={props.endTime}
            onChange={e =>
              props.updateValue(props.index, "endTime", e.target.value)
            }
          />
        </Control>
      </Field>
      <Field>
        <Label>Location</Label>
        <Control>
          <Input
            type="text"
            value={props.location}
            onChange={e =>
              props.updateValue(props.index, "location", e.target.value)
            }
          />
        </Control>
      </Field>
      <Field>
        <Label>Description</Label>
        <Control>
          <TextArea
            value={props.description}
            onChange={e =>
              props.updateValue(props.index, "description", e.target.value)
            }
          />
        </Control>
      </Field>
      <Field>
        <Label>Image Link</Label>
        <Control>
          <Input
            type="url"
            value={props.imageURL}
            onChange={e =>
              props.updateValue(props.index, "imageURL", e.target.value)
            }
            placeholder="https://i.imgur.com/PH6Yj0F.png"
          />
        </Control>
      </Field>
      <Field>
        <Label>Facebook Link</Label>
        <Control>
          <Input
            type="url"
            value={props.facebookURL}
            onChange={e =>
              props.updateValue(props.index, "facebookURL", e.target.value)
            }
            placeholder="www.facebook.com/events/574206449784456"
          />
        </Control>
      </Field>
    </Box>
  );
}

export default EventCard;
