import React from "react";

import { Box, Input } from "bloomer";

function TitleEditorCard(props) {
  return (
    <Box
      className="EventCard TitleEditorCard"
      style={{
        width: "25vw",
        padding: "0.75rem 0.5rem",
        border: "1px solid #d22820"
      }}
    >
      <div class="field is-horizontal">
        <div class="field-label is-normal">
          <label class="label">Title</label>
        </div>
        <div class="field-body">
          <div class="field">
            <p class="control">
              <Input
                type="text"
                value={props.emailTitle}
                onChange={e =>
                  props.updateTitleValue("emailTitle", e.target.value)
                }
              />
            </p>
          </div>
        </div>
      </div>
      <div class="field is-horizontal">
        <div class="field-label is-normal">
          <label class="label">Subtitle</label>
        </div>
        <div class="field-body">
          <div class="field">
            <p class="control">
              <Input
                type="text"
                value={props.emailSubtitle}
                onChange={e =>
                  props.updateTitleValue("emailSubtitle", e.target.value)
                }
              />
            </p>
          </div>
        </div>
      </div>
    </Box>
  );
}

export default TitleEditorCard;
